  .container {
     display: flex;
     align-items: center;
     justify-content: center;
     text-align: center;
     background-position: 50%, 50%;
     background-size:cover;
     background-repeat: no-repeat;


  }
  .wrapperText {
      display: flex;
      flex-direction: column;
      align-items: center;
  }

  @media screen and (min-width: 360px) {
      .container {
          min-width: 360px;
          background-image: url(./heroMobile.jpg);
          padding: 40px 16px 71px 16px;
        
      }
          .wrapperText {
              width: 328px;
          }
                   @media (min-device-pixel-ratio: 2),
                   (min-resolution: 192dpi),
                   (min-resolution: 2dppx) {
                    .container {
                            background-image: url(./phone2x.jpg);
                          
                    
                        }
                   }
  }

  @media screen and (min-width: 768px) {
      .container {
          min-width: 768px;
          background-image: url(./heroTablet.jpg);
          padding: 89px 194px 88px 194px;


      }
          .wrapperText {
              width: 380px;
          }
         @media (min-device-pixel-ratio: 2),
         (min-resolution: 192dpi),
         (min-resolution: 2dppx) {
             .container {
                 background-image: url(./tablet2x.jpg);
    
    
             }
         }

  }

  @media screen and (min-width: 1024px) {
      .container {
          background-image: url(./desktop.jpg);
          padding: 164px 322px 163px 322px;
          min-width: 1024px;



      }
          .wrapperText {
             width: 380px;
          }
                   @media (min-device-pixel-ratio: 2),
                   (min-resolution: 192dpi),
                   (min-resolution: 2dppx) {
                       .container {
                           background-image: url(./desktop2x.jpg);
        
        
                       }
                   }

  }

  @media screen and (min-width: 2560px) {
      .container {
          background-image: url(./desktopmask.jpg);
          min-width: 1170px;
          padding: 164px 395px 163px 395px;
          background-size: 1170px;
          background-size: contain
      }
          .wrapperText {
              width: 380px;
          }
                   @media (min-device-pixel-ratio: 2),
                   (min-resolution: 192dpi),
                   (min-resolution: 2dppx) {
                       .container {
                           background-image: url(./desktop2x.jpg);
        
        
                       }
                   }
  }
  
